@keyframes wobble-hor-bottom {
  0%, 100% {
    transform-origin: 50%;
    transform: translateX(0%);
  }

  15% {
    transform: translateX(-30px)rotate(-6deg);
  }

  30% {
    transform: translateX(15px)rotate(6deg);
  }

  45% {
    transform: translateX(-15px)rotate(-3.6deg);
  }

  60% {
    transform: translateX(9px)rotate(2.4deg);
  }

  75% {
    transform: translateX(-6px)rotate(-1.2deg);
  }
}

.backdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  visibility: visible;
  background-color: rgba(94, 99, 99, .7);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
}

.backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
}

.backdrop.is-hidden .modal {
  transform: translate(-50%, -50%)rotate(360deg);
}

.modal {
  width: 458px;
  height: 450px;
  background-color: #ffe550;
  border-radius: 30px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)rotate(0deg);
  box-shadow: 0 4px 4px rgba(0, 0, 0, .06);
}

.close-button {
  width: 32px;
  height: 32px;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, .1);
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: box-shadow .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 16px;
  right: 16px;
}

.close-button:hover {
  box-shadow: 0 1px 6px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 2px 1px rgba(46, 47, 66, .08);
}

.close-icon {
  width: 11px;
  height: 11px;
  fill: #000;
}

